.header {
  background: url("../img/header.jpg") no-repeat center 0;
  background-size: cover;
  height: 50vh;
  padding: 0px;
  margin-bottom: 50px;
}

.header .nav {
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header .nav .menu {
  display: flex;
  justify-content: space-around;
}

.header .nav .menu__list ul {
  display: flex;
}

.header .nav .menu__list ul li {
  margin-right: 10px;
}

.header .nav .menu.open {
  display: block;
}

.header .nav .hamburger {
  display: none;
  width: 35px;
  height: 30px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  margin-left: 10%;
}

.header .nav .hamburger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background-color: #000;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

.header .nav .hamburger span:nth-child(1) {
  top: 0px;
}

.header .nav .hamburger span:nth-child(2),
.header .nav .hamburger span:nth-child(3) {
  top: 10px;
}

.header .nav .hamburger span:nth-child(4) {
  top: 20px;
}

.header .nav .hamburger.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header .nav .hamburger.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.header .nav .hamburger.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.header .nav .hamburger.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header .title {
  text-align: center;
}

.header .title h1 {
  font-size: 80px;
  font-weight: bold;
  letter-spacing: 15px;
  color: #222;
  padding-top: 10%;
}

.section .wrapper .title h2 {
  font-size: 40px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 50px;
}

.section .wrapper .items {
  display: flex;
}

.section .wrapper .items__image {
  width: calc(41.66666666666667% - 30px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.section .wrapper .contact {
  margin-top: 10%;
  margin-bottom: 50px;
}

.section .wrapper .contact .rows {
  display: flex;
}

.section .wrapper .contact .rows__map {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
}

.section .wrapper .contact .rows__phone {
  width: calc(50% - 30px);
  border: 1px solid transparent;
  border-radius: 100px 50px;
  background-color: #ccc;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.section .wrapper .contact .rows__phone ul {
  text-align: center;
  margin-top: auto auto;
}

.section .about__title h2 {
  font-size: 40px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 50px;
}

.section .about__us .us__items {
  display: flex;
}

.section .about__us .us__items .item {
  width: calc(33.333333333333336% - 30px);
}

.section .about__us .us__items .item img {
  height: 100%;
}

.footer {
  margin-top: 100px;
  background-color: #666;
  padding: 20px;
}

body {
  font-family: 'Roboto Mono', monospace;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: #000;
  outline: none;
}

a:hover {
  text-decoration: underline;
  color: #CED6D6;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 460px) {
  .section .wrapper .items__image {
    margin-left: 10px;
    margin-right: 10px;
  }

  .section .wrapper .contact .rows__map {
    margin-left: 10px;
    margin-right: 10px;
  }

  .section .wrapper .contact .rows__phone {
    margin-left: 10px;
    margin-right: 10px;
  }

  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}